'use strict';

export default categories = {

  INIT_STATE: () => ({
    fetching: false,
    selected: null,
    data: []
  }),

  CATEGORIES_SELECT: (state, data) => ({
    ...state,
    selected: data
  }),

  CATEGORIES_REQUEST: state => ({
    ...state,
    fetching: true
  }),

  CATEGORIES_RECEIVED (state, data = null) {
    if(data) return {
      ...state,
      fetching: false,
      data
    }
    return state;
  }

}




